"use client"
import { useState } from "react"
import { useSession } from "next-auth/react"
import React from 'react'
import { updateProfile, fetchuser } from "@/actions/useractions"

const Dashboard = () => {
  const {data: session, update} = useSession()
  const [form, setform] = useState({})

  const handleChange = (e) => {
    setform({ ...form, [e.target.name]: e.target.value })
  }

  const handleSubmit = async (e) => {
    update()
    let a = update
  }

  return (
    <div className='flex flex-col justify-center items-center mx-auto gap-1'>
      <h2 className='text-2xl my-7 font-bold'>Wellcome to your Dashboard</h2>
      <form className="flex flex-col gap-1 pt-1 w-[40%]" onSubmit={handleSubmit}>
        <label className="flex flex-col text-sm font-medium text-gray-200">
          Name
          <input value={form.name?form.name:""} onChange={handleChange} name="name" id="name" type="text" className='bg-slate-800 p-2 rounded-lg mt-1' />
        </label>
        <label className="flex flex-col text-sm font-medium text-gray-200">
          Email
          <input value={form.email?form.email:""} onChange={handleChange} name="email" id="email" type="text" className='bg-slate-800 p-2 rounded-lg mt-1' />
        </label>
        <label className="flex flex-col text-sm font-medium text-gray-200">
          Username
          <input value={form.username?form.username:""} onChange={handleChange} name="username" id="username" type="text" className='bg-slate-800 p-2 rounded-lg mt-1' />
        </label>
        <label className="flex flex-col text-sm font-medium text-gray-200">
          Profile Picture URL
          <input value={form.profile?form.profile:""} onChange={handleChange} name="profile" id="profile" type="text" className='bg-slate-800 p-2 rounded-lg mt-1' />
        </label>
        <label className="flex flex-col text-sm font-medium text-gray-200">
          Cover Picture URL
          <input value={form.cover?form.cover:""} onChange={handleChange} name="cover" id="cover" type="text" className='bg-slate-800 p-2 rounded-lg mt-1' />
        </label>
        <label className="flex flex-col text-sm font-medium text-gray-200">
          Razorpay Key ID
          <input value={form.razorpayid?form.razorpayid:""} onChange={handleChange} name="razorpayid" id="razorpayid" type="text" className='bg-slate-800 p-2 rounded-lg mt-1' />
        </label>
        <label className="flex flex-col text-sm font-medium text-gray-200">
          Razorpay Secret Key
          <input value={form.secret?form.secret:""} onChange={handleChange} name="secret" id="secret" type="text" className='bg-slate-800 p-2 rounded-lg mt-1' />
        </label>
        <div className="flex gap-2 pt-2">
          <button className="bg-blue-700 w-full cursor-pointer text-white rounded py-2 px-3 hover:bg-blue-600 transition">
            Save
          </button>
        </div>
      </form>
    </div>
  )
}

export default Dashboard
